

@import url("https://fonts.googleapis.com/css?family=Patrick+Hand+SC|Roboto&display=swap");
@font-face {
  font-family: myFirstFont;
  /* src: url("./src/assets/Kanit\ Light.ttf"); */
  src: url("../src/assets/fonts/Kanit Light.ttf");
}
body {
  font-family: myFirstFont;
}
/*================= navBar-css =================*/
.nav-box {
  min-width: 100vh;
  background-color: #240f03;
  padding: 1rem 10rem;
  color: rgb(255, 255, 255);
  display: flex;
  flex-direction: row;
  justify-items: center;
}
.nav-head {
  font-weight: bold;
  flex-grow: 1;
  font-size: 1.2rem;
  cursor: pointer;
}
.nav-sub {
  font-size: 1rem;
  margin-left: 1.5rem;
  cursor: pointer;
  font-weight: 600;
}
.nav-icons {
  font-size: 1.3rem;
  color: rgb(163, 162, 162);
  cursor: pointer;
  margin-left: 1rem;
}
.nav-icons img {
  margin-top: -0.5rem;
  height: 1.4rem;
}
.nav-select {
  border-bottom: 1px solid rgb(255, 255, 255);
}
/*================= home-css =================*/
.home-containers {
  background-color: #240f03;
  height: 81vh;
}
.home-box {
  min-width: 80rem;
  display: flex;
  margin: auto;
  flex-direction: row;
  height: 35rem;
}

.home-item {
  min-width: 35%;
}
.home-title{
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 2rem;
  min-width: 65%;
  height: 100%;
  color: rgb(206, 206, 206);

}
.title-item{
  text-align: center;
}
.home-item img {
  height: 100%;
}
.home-img {
  min-width: 65%;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  color: rgb(206, 206, 206);
}
/* ================ portfolios ============ */
.port-box{
  background-color: #240f03c2;
  text-align: center;
  padding: 3rem;
  color: rgb(255, 255, 255);
}
.port-box img{
  width: 80%;
}
/* ================ project =============== */
.pj-content {
  max-width: 90rem;
  margin: auto;
  display: flex;
  flex-direction: row;
}
.pj-box {
  display: flex;
  color: rgb(255, 255, 255);
  flex-direction: row;
  /* width: 90%; */
  margin: 0 auto;
  padding: 3rem;

  /* border: 1px solid black;   */
  background-color: #240f03;
}
.pj-box2 {
  display: flex;
  color: rgb(255, 255, 255);
  flex-direction: row;
  /* width: 90%; */
  margin: 0 auto;
  padding: 3rem;
  /* border: 1px solid black;   */
  background-color: #240f03cc;
}
.pj-img {
  min-width: 40%;
  min-height: 10rem;
  max-height: 25rem;
  padding: 1rem;
  justify-content: center;
  justify-items: center;
  display: flex;
  flex-direction: row;
  /* border: 1px solid black; */
}
.pj-title {
  padding: 2rem;
}
.pj-img img {
  min-width: 30rem;
  max-width: 30rem;
  min-height: 18rem;
  max-height: 18rem;
  border-radius: 0.2rem;
}
.pj-skills {
  margin-top: 1rem;
  display: flex;
  flex-direction: row;
}
.pj-item {
  /* min-width: 4rem; */
  background-color: rgba(129, 88, 129, 0.938);
  margin-left: 0.5rem;
  /* min-height: 7rem; */
  padding: 1.2rem;
  border-radius: 10rem;
  text-align: center;
  /* border: 1px solid black; */
}
.pj-item img {
  /* width: 8rem; */
  height: 3rem;
}
/* ======================contact-css============ */
.box-contact {
  color: rgb(255, 255, 255);
  background-color: #240f03;
  padding: 3rem;
}
.content-contact{
  display: flex;
  width: 80rem;
  margin: auto;
}
.content-contact-l{
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 40%;
}
.content-contact-l img{
  padding: 2rem;
  height: 30rem;
  background-color: #fffb00;
  border-radius: 1rem;
  border-bottom-left-radius: 15rem;
}
.content-contact-r{
  width: 60%;
  /* background-color: darkblue; */
}
.contact-title{
  display: flex;
  align-items: center;
  justify-content: center;
}
.contact-title h2{
  margin-left: 5rem;
  flex-grow: 1;
  margin-right: 2rem;
}
.contact-title img{
  width: 5rem;
  margin-right: 1rem;
}
.content-contact textarea {
  height: 8rem;
  resize: none;
}
.box-contact-sc{
  background-color: #240f03cc;
  /* color: rgb(255, 255, 255); */
  font-weight: 900;
  padding:  3rem;
  color: #fffb00;
}
.box-contact-sc a:visited, a:link, a:hover {
  text-decoration:none;
  color: #fffb00;
}
.contact-item{
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.contact-item-contants{
  width: 10rem;
  font-size: 1.6rem;
  font-weight: 900;
  border-radius: 1rem;
  display: flex;
  text-align: center;
  align-items: center;
  justify-content: center;
  height: 8rem;
  background-color: #746257cc;
  margin: 0.5rem 1.3rem;
  cursor: pointer;
}
.contact-item-contants img{
  width: 4rem;
  border-radius: 1rem;
}
.contact-item-contants div{
  margin: 0 1.2rem;
  height: 6.3rem;
  /* background-color: #110600; */
}
.contact-item-contants p{
  margin-top: 0.5rem;
}

/*================= footer-css =================*/
.footer {
  padding: 1rem;
  display: flex;
  align-items: center;
  background-color: #110600;
  justify-content: center;
  /* height: 4rem; */
  color: rgb(206, 206, 206);
}

.footer img {
  height: 2rem;
  cursor: pointer;
}

.footer-nav {
  display: flex;
  margin-top: 1rem;
}

.footer-nav p {
  display: flex;
  font-weight: bold;
  cursor: pointer;
}

.footer-nav p:hover {
  font-weight: 900;
  color: rgb(97, 79, 64);
}
